import React, { useEffect, useState } from 'react'
import axios from 'axios'
import MaterialTable from 'material-table'
import Notification from '../../../component/AlertMessageBox';
import ConfirmDialog from "../../../component/ConfirmDialog";
import { Edit } from '@material-ui/icons'
import BuildIcon from '@material-ui/icons/Build';
import DoneOutlineIcon from '@material-ui/icons/DoneOutline';
import ClearIcon from '@material-ui/icons/Clear';
const Colors = () => {
  const [usersData, setUsersData] = useState([]);
  const [notify, setNotify] = useState({ isOpen: false, message: '', type: '' })
  const [confirmDialog, setConfirmDialog] = useState({ isOpen: false, title: '', subTitle: '' })
  const apiurl = 'http://localhost:3000/admin';
  const authRequest = axios.create({
    baseURL: apiurl,
    headers: {
      'auth-token': 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpZCI6IjYwNWZhNDliMjUzNjI1MjliMDMzMTBhMyIsImlhdCI6MTYxNzI3OTI5NSwiZXhwIjoxNjE3MzY1Njk1fQ.vUJVRdhU3sMKhnCrZIE9W-7fIAumUNQJ-N8vOIpP3XY'
    }
  })

  const columns = [
    { title: "الصورة ", field: "photo", render: rowData => <img src={`https://jetek-devolopment.herokuapp.com/${rowData.photo}`} style={{ width: 50, borderRadius: '50%' }} /> },
    { title: "الاسم الاول ", field: "firstName" },
    { title: "الاسم الاخير", field: "lastName" },
    { title: "الحساب مفعل", field: "confirmed", render: rowData => rowData.confirmed ? <div style={{ width: 50, borderRadius: '50%' }}> <DoneOutlineIcon />  </div> : <div style={{ width: 50, borderRadius: '50%' }}> <ClearIcon /> </div> },
    { title: "رقم الهاتف", field: "phone" },
  ]
  const fetch = async () => {
    try {
      const users = await authRequest.get(`/getAllUsers`)
      // handle success
      // console.log(users.data);
      await setUsersData(users.data)
      // console.log("state seting");

    } catch (error) {
      console.log(error);
    }

  }

  const deleteUser = async (id) => {
    // console.log(id);
    setConfirmDialog({
      ...confirmDialog,
      isOpen: false
    })
    var body = {
      "id": id
    }

    // const users = await authRequest.post(`/deleteUser`)

    axios.post('http://localhost:3000//admin/deleteUser', body)
      .then(response => {
        console.log(response);
        if (response.status == 200) {
          fetch()

          setNotify({
            isOpen: true,
            message: 'تم حذف المستخدم',
            type: 'error'
          })
        }
      }).catch(err => console.log(err));

  }

  const disableUser = async (id) => {
    setConfirmDialog({
      ...confirmDialog,
      isOpen: false
    })
    // console.log(id);
    var body = {
      "id": id
    }
    // const users = await authRequest.post(`/deleteUser`)

    axios.post('http://localhost:3000//admin/disableUser', body)
      .then(response => {
        console.log(response);
        if (response.status == 200) {
          fetch()

          setNotify({
            isOpen: true,
            message: 'تم تعديل  المستخدم',
            type: 'success'
          })
        }
      }).catch(err => console.log(err));

  }
  useEffect(() => {

    fetch()
  }, [1000])
  return (
    <>
      <h1>العملاء</h1>

      <div className="App">
        <br />
        <MaterialTable
          title="بيانات المستخدمين"
          data={usersData}
          columns={columns}
          options={{
            rowStyle: {
              backgroundColor: '#EEE',
            },
            exportButton: true,
          }}
          actions={[
            {
              icon: () => < BuildIcon />,
              tooltip: 'تعطيل الحساب ',
              onClick: (event, rowData) => {
                setConfirmDialog({
                  isOpen: true,
                  title: ' هل انت متاكد من تغيير حالة المستخدم ؟ ',
                  // subTitle: "لا يمكن التراجع عن هذة العملية ",
                  onConfirm: () => { disableUser(rowData._id) }
                })
              }
            },
            {
              icon: 'delete',
              tooltip: 'حذف المستخدم',
              onClick: (event, rowData) => {
                setConfirmDialog({
                  isOpen: true,
                  title: ' هل انت متاكد من حذف المستخدم ؟ ',
                  subTitle: "لا يمكن التراجع عن هذة العملية ",
                  onConfirm: () => { deleteUser(rowData._id) }
                })
              }
            }
          ]}
        />
      </div>
      <Notification
        notify={notify}
        setNotify={setNotify}
      />
      <ConfirmDialog
        confirmDialog={confirmDialog}
        setConfirmDialog={setConfirmDialog}
      />
    </>
  )
}

export default Colors
